<template>
  <div class="news">
    <ul class="news-list" v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10">
      <li v-for="item in list">
        <router-link :to="{ path: 'newsdetail', query: { docid: item.docid }}">
          <div class="left news-thumb">
            <img :src="item.imgsrc">
          </div>
          <div class="left news-info">
            <div class="news-title">{{item.title}}</div>
            <p>{{item.ptime}}   <i class="right">{{item.commentCount}}</i></p>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
    import Vue from 'vue'
    import {
        InfiniteScroll
    } from 'mint-ui';

    Vue.use(InfiniteScroll);
    import {
        Indicator
    } from 'mint-ui';
    import api from '../utils/api'

    export default {
        name: 'news',
        data() {
            return {
                list: [],
                page: 1,
                loading: true
            }
        },
        created() {
            this.page = 1;
            this.list = [];
            this.new_list();
        },
        methods: {
            new_list() {
                Indicator.open('加载中...');
                api.new_list({
                        page: this.page
                    })
                    .then(function(res) {
                        this.list = (this.list).concat(res.data);
                        this.list.reverse()
                        this.page++;
                        Indicator.close();
                    }.bind(this)).catch(function(error) {
                        console.log(error)
                    })
            },
            loadMore() {
                this.loading = true;
                setTimeout(() => {
                    this.new_list()
                    this.loading = false;
                }, 2000);
            }
        }
    }
</script>


<style>
    .news-thumb {
        flex: 3;
    }

    .news-thumb img {
        width: 90px;
        height: 60px;
    }

    .news-list li {
        box-sizing: border-box;
        padding: 10px 15px;
        border-bottom: 1px solid #e3e3e3;
        height: auto;
        overflow: hidden;
        display: flex;
    }

    .news-list li a {
        display: block;
        display: flex;
    }

    .news-info {
        flex: 7;
        padding-left: 5px;
        box-sizing: border-box;
    }

    .news-title {
        width: 100%;
        font-size: 14px;
        height: 38px;
        overflow: hidden;
    }

    .news-info p {
        color: #666;
        font-size: 12px;
        margin-top: 5px;
    }

    .news-info i {
        font-style: normal;
        padding: 2px 20px 2px 10px;
        background: url("data:image/png;base64,") center right 3px no-repeat;
        background-size: 14px;
        text-align: right;
        display: block;
    }
</style>



